@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap");

$font-1: "Poppins", sans-serif;





/* theme 1*/
$text1: hsl(240, 21%, 24%);
$background1: hsl(240, 33%, 71%);
$theme1: hsl(240, 18%, 38%);
/* theme 2*/
$text1: hsl(353, 62%, 14%);
$background1: hsl(350, 33%, 62%);
$theme1: hsl(359, 22%, 37%);
/* theme 3*/
/* theme 4*/
/* theme 5*/
@media (min-width: 700px){
  body {
    margin: 0;
    padding: 0;
    font-family: $font-1;
    .container{
      width: 100vw;
      height: 100vh;
      display: flex;
      flex-direction: column;
      transition: all 1.4s ease;
    }
  }
}
@media (max-width: 700px) {
  body {
    margin: 0;
    padding: 0;
    font-family: $font-1;

    .container{
      width: 100%;
      height: 100vh;
      display: flex;
      flex-direction: column;
    }
  }
}
